<!-- src/components/ModelViewer.vue -->
<template>
  <div ref="container" class="model-viewer">
    <canvas ref="canvas"></canvas>
    <div v-if="loading" class="loading">...</div>
    <div v-if="error" class="error">{{ error }}</div>
  </div>
</template>

<script>
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader.js'
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader.js'

export default {
  name: 'ModelViewer',
  props: {
    modelPath: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      scene: null,
      camera: null,
      renderer: null,
      controls: null,
      model: null,
      loading: false,
      error: null,
      animationId: null,
      // 
      lastFrameTime: 0,
      frameInterval: 1000 / 60, // 60fps
      needsRender: true // 
    }
  },
  mounted() {
    this.init()
    this.loadModel()
    window.addEventListener('resize', this.onWindowResize)
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.onWindowResize)
    if (this.animationId) {
      cancelAnimationFrame(this.animationId)
    }
    if (this.renderer) {
      this.renderer.dispose()
    }
    if (this.controls) {
      this.controls.dispose()
    }
  },
  methods: {
    init() {
      // 
      if (!this.$refs.container) {
        console.error('ModelViewer container reference is not available')
        return
      }
      
      const container = this.$refs.container
      const width = container.clientWidth
      const height = container.clientHeight

      // 
      this.scene = new THREE.Scene()
      this.scene.background = null // null

      // 
      // 
      const ambientLight = new THREE.AmbientLight(0xffffff, 0.6)
      this.scene.add(ambientLight)

      // 
      const directionalLight1 = new THREE.DirectionalLight(0xffffff, 1.0)
      directionalLight1.position.set(5, 10, 7)
      directionalLight1.castShadow = true // 
      this.scene.add(directionalLight1)

      // 
      const directionalLight2 = new THREE.DirectionalLight(0xffeedd, 0.5)
      directionalLight2.position.set(-5, -5, -5)
      this.scene.add(directionalLight2)

      // 
      const hemisphereLight = new THREE.HemisphereLight(0xffffbb, 0x080820, 0.3)
      this.scene.add(hemisphereLight)

      // 
      this.camera = new THREE.PerspectiveCamera(60, width / height, 0.1, 10000)
      this.camera.position.set(0, 5, 15)

      // 
      this.renderer = new THREE.WebGLRenderer({
        canvas: this.$refs.canvas,
        antialias: true,
        alpha: true, // 
        powerPreference: "high-performance", // GPU
        stencil: false, // 
        depth: true,
        logarithmicDepthBuffer: true // 
      })
      
      // 
      this.renderer.setSize(width, height)
      this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
      this.renderer.outputEncoding = THREE.sRGBEncoding
      this.renderer.toneMapping = THREE.ACESFilmicToneMapping
      this.renderer.toneMappingExposure = 1.2
      
      // 
      this.renderer.shadowMap.enabled = true
      this.renderer.shadowMap.type = THREE.PCFSoftShadowMap

      // 
      this.controls = new OrbitControls(this.camera, this.renderer.domElement)
      this.controls.enableDamping = true
      this.controls.dampingFactor = 0.05
      this.controls.screenSpacePanning = false
      this.controls.minDistance = 5
      this.controls.maxDistance = 100
      
      // 
      this.controls.minPolarAngle = Math.PI / 6;  // 30
      this.controls.maxPolarAngle = Math.PI * 5 / 6;  // 150
      
      // 
      this.controls.addEventListener('change', () => {
        this.needsRender = true
      })

      // 
      this.animate()
    },
    loadModel() {
      this.loading = true
      this.error = null
      
      // 
      const basePath = this.modelPath.substring(0, this.modelPath.lastIndexOf('/') + 1)
      const modelName = this.modelPath.substring(this.modelPath.lastIndexOf('/') + 1)
      
      console.log(':', basePath)
      console.log(':', modelName)
      
      // MTL
      this.loadModelWithMaterials(basePath, modelName)
    },
    loadModelWithMaterials(basePath, modelName) {
      const mtlLoader = new MTLLoader()
      mtlLoader.setPath(basePath)
      
      // OBJMTL
      // aa.obj -> aa.mtl, xx.obj -> xx.mtl
      const mtlName = modelName.replace(/\.obj$/i, '.mtl');
      
      console.log(':', mtlName)
      
      mtlLoader.load(
        mtlName,
        (materials) => {
          console.log(':', materials)
          materials.preload()
          
          // OBJ
          const objLoader = new OBJLoader()
          objLoader.setMaterials(materials)
          
          objLoader.load(
            this.modelPath,
            (object) => {
              this.loading = false
              console.log(':', object)
              this.onModelLoad(object)
            },
            (progress) => {
              if (progress.total > 0) {
                const percent = (progress.loaded / progress.total * 100).toFixed(2)
                console.log(`: ${percent}%`)
              }
            },
            (error) => {
              console.warn(':', error)
              this.loading = false
              this.loadModelWithoutMaterials()
            }
          )
        },
        (progress) => {
          if (progress.total > 0) {
            const percent = (progress.loaded / progress.total * 100).toFixed(2)
            console.log(`: ${percent}%`)
          }
        },
        (error) => {
          console.warn(':', error)
          this.loadModelWithoutMaterials()
        }
      )
    },
    loadModelWithoutMaterials() {
      const objLoader = new OBJLoader()
      
      objLoader.load(
        this.modelPath,
        (object) => {
          this.loading = false
          console.log(':', object)
          this.onModelLoad(object)
        },
        (progress) => {
          if (progress.total > 0) {
            const percent = (progress.loaded / progress.total * 100).toFixed(2)
            console.log(`: ${percent}%`)
          }
        },
        (error) => {
          this.loading = false
          this.error = ': ' + (error.message || '')
          console.error(':', error)
        }
      )
    },
    onModelLoad(object) {
      try {
        if (this.model) {
          this.scene.remove(this.model)
        }
        
        this.model = object
        
        // 
        let meshCount = 0
        object.traverse((child) => {
          if (child.isMesh) {
            meshCount++
            console.log(':', child.name || `mesh_${meshCount}`)
            console.log(':', child.material)
            
            // 
            child.frustumCulled = true
            
            // 
            if (!child.material) {
              // 
              child.material = new THREE.MeshStandardMaterial({
                color: 0xcccccc,
                roughness: 0.4,
                metalness: 0.1,
                side: THREE.FrontSide // 
              })
              console.log('')
            } else if (Array.isArray(child.material)) {
              // 
              child.material.forEach((material, index) => {
                if (material) {
                  material.side = THREE.FrontSide
                  material.needsUpdate = true
                  console.log(`[${index}]:`, material)
                }
              })
            } else {
              // 
              child.material.side = THREE.FrontSide
              child.material.needsUpdate = true
              console.log(':', child.material)
              
              // 
              if (child.material.color && child.material.color.getHexString() === 'ffffff') {
                console.warn('')
              }
            }
            
            // 
            child.castShadow = true
            child.receiveShadow = true
          }
        })
        
        // 
        const box = new THREE.Box3().setFromObject(object)
        const size = box.getSize(new THREE.Vector3())
        const center = box.getCenter(new THREE.Vector3())
        
        console.log(':', { box, size, center })
        
        // 
        if (size.length() < 0.1) {
          const scale = 1 / size.length() * 2
          object.scale.set(scale, scale, scale)
        } else if (size.length() > 1000) {
          const scale = 50 / size.length()
          object.scale.set(scale, scale, scale)
        }
        
        // 
        const adjustedBox = new THREE.Box3().setFromObject(object)
        const adjustedCenter = adjustedBox.getCenter(new THREE.Vector3())
        object.position.sub(adjustedCenter)
        
        // 
        const adjustedSize = adjustedBox.getSize(new THREE.Vector3())
        const maxDim = Math.max(adjustedSize.x, adjustedSize.y, adjustedSize.z)
        const fov = this.camera.fov * (Math.PI / 180)
        let cameraZ = Math.abs(maxDim / 2 / Math.tan(fov / 2))
        cameraZ *= 1.5 // 
        
        cameraZ = Math.max(cameraZ, 10)
        cameraZ = Math.min(cameraZ, 500)
        
        this.camera.position.set(0, maxDim * 0.3, cameraZ)
        this.camera.lookAt(0, 0, 0)
        
        this.controls.target.set(0, 0, 0)
        this.controls.update()
        
        this.scene.add(object)
        
        // 
        this.needsRender = true
        
        console.log('')
      } catch (e) {
        this.error = ': ' + e.message
        console.error(':', e)
      }
    },
    onWindowResize() {
      // 
      if (!this.$refs.container) {
        return
      }
      
      const container = this.$refs.container
      const width = container.clientWidth
      const height = container.clientHeight
      
      this.camera.aspect = width / height
      this.camera.updateProjectionMatrix()
      this.renderer.setSize(width, height)
      
      // 
      this.needsRender = true
    },
    animate(time) {
      // 
      if (time - this.lastFrameTime >= this.frameInterval) {
        this.lastFrameTime = time
        
        // 
        if (this.controls && this.controls.enabled) {
          const dampingUpdated = this.controls.update()
          // 
          if (dampingUpdated || this.needsRender) {
            if (this.renderer && this.scene && this.camera) {
              this.renderer.render(this.scene, this.camera)
              this.needsRender = false // 
            }
          }
        } else if (this.needsRender) {
          // 
          if (this.renderer && this.scene && this.camera) {
            this.renderer.render(this.scene, this.camera)
            this.needsRender = false
          }
        }
      }
      
      this.animationId = requestAnimationFrame(this.animate)
    }
  }
}
</script>

<style scoped>
.model-viewer {
  width: 100%;
  height: 100%;
  position: relative;
}

.model-viewer canvas {
  width: 100%;
  height: 100%;
  display: block;
  /*  */
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
}

.loading, .error {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 16px;
  text-align: center;
  background: rgba(0, 0, 0, 0.7);
  padding: 15px 25px;
  border-radius: 8px;
  z-index: 10;
  /*  */
  transform: translate(-50%, -50%) translateZ(0);
}

.error {
  color: #ff6b6b;
  background: rgba(100, 0, 0, 0.7);
}
</style>