            energySaving: '12450.8',
            modelPath: '/ESP/aa.obj' //  public OBJ
        }
    },
    watch: {
        // buildingId
        '$route.query.buildingId': {
            handler(newBuildingId) {
                if (newBuildingId) {
                    this.buildingId = newBuildingId;
                    this.loadBuildingData(newBuildingId);
                }
            },
            immediate: true
        }
    },
    mounted() {
        // 
        this.userId = this.$route.query.userId;
        this.buildingId = this.$route.query.buildingId;

        // userIdbuildingId
        console.log('ID:', this.userId);
        console.log('ID:', this.buildingId);

        // 
        if (this.buildingId) {
            this.loadBuildingData(this.buildingId);
        }
    },
    methods: {
        loadBuildingData(buildingId) {
            // ID
            // API
            switch (buildingId) {
                case '1':
                    this.buildName = 'A';
                    this.buildAddress = 'A';
                    this.deviceNum = '1200';
                    this.electMonth = '15624.5';
                    this.waterMonth = '26541.2';
                    break;
                case '2':
                    this.buildName = 'B';
                    this.buildAddress = 'B';
                    this.deviceNum = '800';
                    this.electMonth = '10124.3';
                    this.waterMonth = '21651.7';
                    break;
                case '3':
                    this.buildName = 'C';
                    this.buildAddress = 'C';
                    this.deviceNum = '1500';
                    this.electMonth = '18424.8';
                    this.waterMonth = '31651.1';
                    break;
                default:
                    this.buildName = '';
                    this.buildAddress = 'AAAA';
                    this.deviceNum = '1000';
                    this.electMonth = '12224.2';
                    this.waterMonth = '24651.3';
            }

            // 
            // API
            console.log(` ${buildingId} `);
        }
    }
}
